import { useState, useEffect } from 'react';
import {
  FileText, Search, Book, Download, Eye, X, Calendar,
  Users, FileCheck, Package, Tag, Filter, ChevronRight,
  ExternalLink, Star, TrendingUp, Clock, CheckCircle, Award
} from 'lucide-react';
import { db } from '../../lib/db';

interface Product {
  id: string;
  name: string;
  sku: string;
  category: string;
}

interface Documentation {
  id: string;
  product_id: string;
  doc_type: string;
  title: string;
  description: string;
  version: string;
  file_size: string;
  page_count: number;
  pdf_url: string;
  cover_image_url: string | null;
  last_updated: string;
  downloads: number;
  product?: Product;
}

export default function Documentation() {
  const [searchQuery, setSearchQuery] = useState('');
  const [selectedCategory, setSelectedCategory] = useState<string>('All');
  const [selectedDocType, setSelectedDocType] = useState<string>('All');
  const [documentation, setDocumentation] = useState<Documentation[]>([]);
  const [isLoading, setIsLoading] = useState(true);
  const [selectedDoc, setSelectedDoc] = useState<Documentation | null>(null);
  const [showPDFViewer, setShowPDFViewer] = useState(false);

  const categories = [
    { id: 'All', name: 'All Products', count: 0 },
    { id: 'Messaging', name: 'Messaging', count: 0 },
    { id: 'Voice', name: 'Voice', count: 0 },
    { id: 'Video', name: 'Video', count: 0 },
    { id: 'Bot', name: 'AI & Bots', count: 0 },
    { id: 'Social', name: 'Social', count: 0 }
  ];

  const docTypes = [
    { id: 'All', name: 'All Types', icon: FileText },
    { id: 'user_guide', name: 'User Guides', icon: Book },
    { id: 'admin_guide', name: 'Admin Guides', icon: Users }
  ];

  useEffect(() => {
    loadDocumentation();
  }, []);

  const loadDocumentation = async () => {
    setIsLoading(true);
    try {
      const { data: productsData, error: productsError } = await db
        .from('products')
        .select('id, name, sku, category')
        .order('name');

      if (productsError) throw productsError;

      const { data: docsData, error: docsError } = await db
        .from('product_documentation')
        .select('*')
        .order('downloads', { ascending: false });

      if (docsError) throw docsError;

      const docsWithProducts = (docsData || []).map((doc: any) => ({
        ...doc,
        product: (productsData || []).find((p: any) => p.id === doc.product_id)
      }));

      setDocumentation(docsWithProducts);
    } catch (error) {
      console.error('Error loading documentation:', error);
    } finally {
      setIsLoading(false);
    }
  };

  const handleDownload = async (doc: Documentation) => {
    await db
      .from('product_documentation')
      .update({ downloads: doc.downloads + 1 })
      .eq('id', doc.id);

    window.open(doc.pdf_url, '_blank');

    await loadDocumentation();
  };

  const handleView = (doc: Documentation) => {
    setSelectedDoc(doc);
    setShowPDFViewer(true);
  };

  const filteredDocs = documentation.filter((doc) => {
    const matchesSearch =
      doc.title.toLowerCase().includes(searchQuery.toLowerCase()) ||
      doc.description.toLowerCase().includes(searchQuery.toLowerCase()) ||
      doc.product?.name.toLowerCase().includes(searchQuery.toLowerCase());

    const matchesCategory =
      selectedCategory === 'All' || doc.product?.category === selectedCategory;

    const matchesDocType =
      selectedDocType === 'All' || doc.doc_type === selectedDocType;

    return matchesSearch && matchesCategory && matchesDocType;
  });

  const getDocTypeColor = (docType: string) => {
    switch (docType) {
      case 'user_guide':
        return 'bg-[#39FF14]/20/20 text-[#39FF14] border-[#39FF14]/30';
      case 'admin_guide':
        return 'bg-[#39FF14]/20 text-[#39FF14] border-[#39FF14]/30';
      case 'api_reference':
        return 'bg-green-500/20 text-green-400 border-green-500/30';
      case 'integration_guide':
        return 'bg-[#39FF14]/20 text-[#39FF14] border-[#39FF14]/30';
      default:
        return 'bg-slate-500/20 text-slate-400 border-slate-500/30';
    }
  };

  const getCategoryColor = (category: string) => {
    switch (category) {
      case 'Messaging':
        return 'text-[#39FF14]';
      case 'Voice':
        return 'text-green-400';
      case 'Video':
        return 'text-[#39FF14]';
      case 'Bot':
        return 'text-[#39FF14]';
      case 'Social':
        return 'text-[#39FF14]';
      default:
        return 'text-slate-400';
    }
  };

  const formatDate = (date: string) => {
    return new Date(date).toLocaleDateString('en-US', {
      year: 'numeric',
      month: 'short',
      day: 'numeric'
    });
  };

  const popularDocs = [...documentation]
    .sort((a, b) => b.downloads - a.downloads)
    .slice(0, 6);

  const recentDocs = [...documentation]
    .sort((a, b) => new Date(b.last_updated).getTime() - new Date(a.last_updated).getTime())
    .slice(0, 6);

  if (isLoading) {
    return (
      <div className="flex-1 overflow-auto bg-[#012419] p-8 flex items-center justify-center">
        <div className="text-center">
          <div className="w-16 h-16 border-4 border-[#39FF14] border-t-transparent rounded-full animate-spin mx-auto mb-4"></div>
          <p className="text-slate-400 text-lg">Loading documentation...</p>
        </div>
      </div>
    );
  }

  return (
    <div className="flex-1 overflow-auto bg-[#012419]">


      <div className="p-8">
        {/* Search and Filters */}
        <div className="mb-8">
          <div className="flex flex-col md:flex-row gap-4 mb-6">
            <div className="flex-1 relative">
              <Search className="absolute left-4 top-1/2 -translate-y-1/2 w-5 h-5 text-slate-400" />
              <input
                type="text"
                placeholder="Search documentation..."
                value={searchQuery}
                onChange={(e) => setSearchQuery(e.target.value)}
                className="w-full bg-slate-800/50 border border-slate-700 rounded-xl pl-12 pr-4 py-3.5 text-white placeholder-slate-500 focus:outline-none focus:border-[#39FF14] focus:ring-2 focus:ring-[#39FF14]/20 transition-all"
              />
              {searchQuery && (
                <button
                  onClick={() => setSearchQuery('')}
                  className="absolute right-4 top-1/2 -translate-y-1/2 text-slate-400 hover:text-white transition-colors"
                >
                  <X className="w-5 h-5" />
                </button>
              )}
            </div>
          </div>

          {/* Category Filters */}
          <div className="flex gap-2 overflow-x-auto pb-2 mb-4">
            {categories.map((category) => {
              const isActive = selectedCategory === category.id;
              return (
                <button
                  key={category.id}
                  onClick={() => setSelectedCategory(category.id)}
                  className={`flex items-center gap-2 px-4 py-2 rounded-lg border-2 font-medium whitespace-nowrap transition-all ${
                    isActive
                      ? 'bg-gradient-to-r from-[#39FF14]/20 to-[#32e012]/20 border-[#39FF14] text-[#39FF14]'
                      : 'bg-slate-800/50 border-slate-700 text-slate-400 hover:border-slate-600 hover:text-slate-300'
                  }`}
                >
                  <Package className="w-4 h-4" />
                  {category.name}
                </button>
              );
            })}
          </div>

          {/* Doc Type Filters */}
          <div className="flex gap-2 overflow-x-auto pb-2">
            {docTypes.map((type) => {
              const Icon = type.icon;
              const isActive = selectedDocType === type.id;
              return (
                <button
                  key={type.id}
                  onClick={() => setSelectedDocType(type.id)}
                  className={`flex items-center gap-2 px-4 py-2 rounded-lg border font-medium whitespace-nowrap transition-all ${
                    isActive
                      ? 'bg-[#39FF14]/20/20 border-[#39FF14] text-[#39FF14]'
                      : 'bg-slate-800/50 border-slate-700 text-slate-400 hover:border-slate-600 hover:text-slate-300'
                  }`}
                >
                  <Icon className="w-4 h-4" />
                  {type.name}
                </button>
              );
            })}
          </div>
        </div>

        {/* Popular Documentation */}
        {searchQuery === '' && selectedCategory === 'All' && selectedDocType === 'All' && (
          <div className="mb-8">
            <div className="flex items-center gap-3 mb-6">
              <Star className="w-6 h-6 text-yellow-400 fill-yellow-400" />
              <h2 className="text-2xl font-bold text-white">Most Popular</h2>
            </div>
            <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-6">
              {popularDocs.map((doc) => (
                <div
                  key={doc.id}
                  className="bg-slate-800/50 border border-slate-700 rounded-xl p-6 hover:border-slate-600 transition-all group"
                >
                  <div className="flex items-start justify-between mb-4">
                    <span
                      className={`px-3 py-1 rounded-lg text-xs font-medium border ${getDocTypeColor(
                        doc.doc_type
                      )}`}
                    >
                      {doc.doc_type.replace('_', ' ').toUpperCase()}
                    </span>
                    <div className="flex items-center gap-1 text-yellow-400">
                      <TrendingUp className="w-4 h-4" />
                      <span className="text-xs font-semibold">{doc.downloads}</span>
                    </div>
                  </div>
                  <h3 className="text-lg font-bold text-white mb-2 group-hover:text-[#39FF14] transition-colors">
                    {doc.title}
                  </h3>
                  <p className="text-sm text-slate-400 mb-3">{doc.product?.name}</p>
                  <p className="text-sm text-slate-400 mb-4 line-clamp-2">
                    {doc.description}
                  </p>
                  <div className="flex items-center justify-between pt-4 border-t border-slate-700">
                    <div className="flex items-center gap-4 text-xs text-slate-500">
                      <span className="flex items-center gap-1">
                        <FileText className="w-3 h-3" />
                        {doc.page_count}p
                      </span>
                      <span>{doc.file_size}</span>
                    </div>
                    <div className="flex gap-2">
                      <button
                        onClick={() => handleView(doc)}
                        className="p-2 bg-[#39FF14]/20/20 hover:bg-[#39FF14]/20/30 text-[#39FF14] rounded-lg transition-colors"
                        title="View"
                      >
                        <Eye className="w-4 h-4" />
                      </button>
                      <button
                        onClick={() => handleDownload(doc)}
                        className="p-2 bg-green-500/20 hover:bg-green-500/30 text-green-400 rounded-lg transition-colors"
                        title="Download"
                      >
                        <Download className="w-4 h-4" />
                      </button>
                    </div>
                  </div>
                </div>
              ))}
            </div>
          </div>
        )}

        {/* Recently Updated */}
        {searchQuery === '' && selectedCategory === 'All' && selectedDocType === 'All' && (
          <div className="mb-8">
            <div className="flex items-center gap-3 mb-6">
              <Clock className="w-6 h-6 text-[#39FF14]" />
              <h2 className="text-2xl font-bold text-white">Recently Updated</h2>
            </div>
            <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-6">
              {recentDocs.map((doc) => (
                <div
                  key={doc.id}
                  className="bg-slate-800/50 border border-slate-700 rounded-xl p-6 hover:border-slate-600 transition-all group"
                >
                  <div className="flex items-start justify-between mb-4">
                    <span
                      className={`px-3 py-1 rounded-lg text-xs font-medium border ${getDocTypeColor(
                        doc.doc_type
                      )}`}
                    >
                      {doc.doc_type.replace('_', ' ').toUpperCase()}
                    </span>
                    <div className="flex items-center gap-1 text-slate-400">
                      <Calendar className="w-3 h-3" />
                      <span className="text-xs">{formatDate(doc.last_updated)}</span>
                    </div>
                  </div>
                  <h3 className="text-lg font-bold text-white mb-2 group-hover:text-[#39FF14] transition-colors">
                    {doc.title}
                  </h3>
                  <p className="text-sm text-slate-400 mb-3">{doc.product?.name}</p>
                  <p className="text-sm text-slate-400 mb-4 line-clamp-2">
                    {doc.description}
                  </p>
                  <div className="flex items-center justify-between pt-4 border-t border-slate-700">
                    <div className="flex items-center gap-4 text-xs text-slate-500">
                      <span className="flex items-center gap-1">
                        <FileText className="w-3 h-3" />
                        {doc.page_count}p
                      </span>
                      <span>v{doc.version}</span>
                    </div>
                    <div className="flex gap-2">
                      <button
                        onClick={() => handleView(doc)}
                        className="p-2 bg-[#39FF14]/20/20 hover:bg-[#39FF14]/20/30 text-[#39FF14] rounded-lg transition-colors"
                        title="View"
                      >
                        <Eye className="w-4 h-4" />
                      </button>
                      <button
                        onClick={() => handleDownload(doc)}
                        className="p-2 bg-green-500/20 hover:bg-green-500/30 text-green-400 rounded-lg transition-colors"
                        title="Download"
                      >
                        <Download className="w-4 h-4" />
                      </button>
                    </div>
                  </div>
                </div>
              ))}
            </div>
          </div>
        )}

        {/* All Documentation */}
        <div>
          <div className="flex items-center justify-between mb-6">
            <h2 className="text-2xl font-bold text-white">
              {searchQuery || selectedCategory !== 'All' || selectedDocType !== 'All'
                ? 'Search Results'
                : 'All Documentation'}
            </h2>
            <p className="text-slate-400">
              Showing <span className="text-white font-semibold">{filteredDocs.length}</span> guide
              {filteredDocs.length !== 1 ? 's' : ''}
            </p>
          </div>

          <div className="grid grid-cols-1 lg:grid-cols-2 gap-6">
            {filteredDocs.map((doc) => (
              <div
                key={doc.id}
                className="bg-slate-800/50 border border-slate-700 rounded-xl overflow-hidden hover:border-slate-600 transition-all group"
              >
                <div className="p-6">
                  <div className="flex items-start justify-between mb-4">
                    <div className="flex items-center gap-3">
                      <div className="p-3 bg-slate-700/50 rounded-lg">
                        <FileText className={`w-6 h-6 ${getCategoryColor(doc.product?.category || '')}`} />
                      </div>
                      <div>
                        <span
                          className={`px-2 py-1 rounded text-xs font-medium border ${getDocTypeColor(
                            doc.doc_type
                          )}`}
                        >
                          {doc.doc_type.replace('_', ' ').toUpperCase()}
                        </span>
                      </div>
                    </div>
                    <div className="text-right">
                      <div className="text-xs text-slate-500 mb-1">Version {doc.version}</div>
                      <div className="flex items-center gap-1 text-xs text-slate-400">
                        <Download className="w-3 h-3" />
                        <span>{doc.downloads}</span>
                      </div>
                    </div>
                  </div>

                  <h3 className="text-xl font-bold text-white mb-2 group-hover:text-[#39FF14] transition-colors">
                    {doc.title}
                  </h3>

                  <div className="flex items-center gap-2 mb-3">
                    <Tag className="w-4 h-4 text-slate-500" />
                    <span className="text-sm text-slate-400">{doc.product?.name}</span>
                  </div>

                  <p className="text-sm text-slate-400 mb-4 line-clamp-2">{doc.description}</p>

                  <div className="flex items-center gap-4 mb-4 text-xs text-slate-500">
                    <span className="flex items-center gap-1">
                      <FileText className="w-4 h-4" />
                      {doc.page_count} pages
                    </span>
                    <span className="flex items-center gap-1">
                      <Package className="w-4 h-4" />
                      {doc.file_size}
                    </span>
                    <span className="flex items-center gap-1">
                      <Calendar className="w-4 h-4" />
                      {formatDate(doc.last_updated)}
                    </span>
                  </div>

                  <div className="flex gap-3">
                    <button
                      onClick={() => handleView(doc)}
                      className="flex-1 bg-[#39FF14] hover:bg-[#32e012] text-white px-4 py-2.5 rounded-lg font-semibold transition-colors flex items-center justify-center gap-2"
                    >
                      <Eye className="w-4 h-4" />
                      View PDF
                    </button>
                    <button
                      onClick={() => handleDownload(doc)}
                      className="flex-1 bg-green-600 hover:bg-green-700 text-white px-4 py-2.5 rounded-lg font-semibold transition-colors flex items-center justify-center gap-2"
                    >
                      <Download className="w-4 h-4" />
                      Download
                    </button>
                  </div>
                </div>
              </div>
            ))}
          </div>

          {filteredDocs.length === 0 && (
            <div className="bg-slate-800/50 border border-slate-700 rounded-2xl p-16 text-center">
              <div className="max-w-md mx-auto">
                <div className="w-20 h-20 bg-slate-700/50 rounded-full flex items-center justify-center mx-auto mb-6">
                  <FileText className="w-10 h-10 text-slate-500" />
                </div>
                <h3 className="text-2xl font-bold text-white mb-3">No documentation found</h3>
                <p className="text-slate-400 mb-6">
                  Try adjusting your search or filters to find what you're looking for.
                </p>
                <button
                  onClick={() => {
                    setSearchQuery('');
                    setSelectedCategory('All');
                    setSelectedDocType('All');
                  }}
                  className="bg-[#39FF14] hover:bg-[#32e012] text-black px-6 py-3 rounded-lg font-semibold transition-colors"
                >
                  Clear All Filters
                </button>
              </div>
            </div>
          )}
        </div>
      </div>

      {/* PDF Viewer Modal */}
      {showPDFViewer && selectedDoc && (
        <div className="fixed inset-0 bg-black/90 backdrop-blur-sm flex items-center justify-center z-50 p-4">
          <div className="bg-slate-900 border border-slate-700 rounded-2xl w-full max-w-6xl h-[90vh] flex flex-col">
            {/* Modal Header */}
            <div className="flex items-center justify-between p-6 border-b border-slate-700">
              <div className="flex-1">
                <h2 className="text-2xl font-bold text-white mb-1">{selectedDoc.title}</h2>
                <div className="flex items-center gap-4 text-sm text-slate-400">
                  <span>{selectedDoc.product?.name}</span>
                  <span>•</span>
                  <span>Version {selectedDoc.version}</span>
                  <span>•</span>
                  <span>{selectedDoc.page_count} pages</span>
                  <span>•</span>
                  <span>{selectedDoc.file_size}</span>
                </div>
              </div>
              <div className="flex items-center gap-3">
                <button
                  onClick={() => handleDownload(selectedDoc)}
                  className="bg-green-600 hover:bg-green-700 text-white px-4 py-2 rounded-lg font-semibold transition-colors flex items-center gap-2"
                >
                  <Download className="w-4 h-4" />
                  Download
                </button>
                <button
                  onClick={() => setShowPDFViewer(false)}
                  className="p-2 hover:bg-slate-800 rounded-lg transition-colors"
                >
                  <X className="w-6 h-6 text-slate-400 hover:text-white" />
                </button>
              </div>
            </div>

            {/* PDF Viewer Area */}
            <div className="flex-1 p-6 overflow-auto">
              <div className="bg-slate-800/50 border-2 border-dashed border-slate-700 rounded-xl h-full flex items-center justify-center">
                <div className="text-center">
                  <FileText className="w-20 h-20 text-slate-600 mx-auto mb-4" />
                  <h3 className="text-xl font-bold text-white mb-2">PDF Preview</h3>
                  <p className="text-slate-400 mb-6 max-w-md">
                    This is a placeholder for the PDF viewer. In production, this would display the actual PDF document using a library like PDF.js or react-pdf.
                  </p>
                  <div className="space-y-3">
                    <button
                      onClick={() => handleDownload(selectedDoc)}
                      className="bg-gradient-to-r from-[#39FF14] to-[#32e012] hover:from-[#32e012] hover:to-[#28b80f] text-black px-6 py-3 rounded-lg font-semibold transition-all flex items-center justify-center gap-2 mx-auto"
                    >
                      <Download className="w-5 h-5" />
                      Download PDF to View
                    </button>
                    <p className="text-sm text-slate-500">
                      PDF URL: <a href={selectedDoc.pdf_url} target="_blank" rel="noopener noreferrer" className="text-[#39FF14] hover:underline">{selectedDoc.pdf_url}</a>
                    </p>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      )}
    </div>
  );
}
